@import "../_tuan-mixins.less";
@import "../_tuan-variables.less";

.fn-search-head{
	@header-height: 44;
	@inner-height: 36;
	@zindex: 200;

	.header{
		height: auto;
	}

	.header-inner{
		height: unit(@header-height * 2/25, rem);
	}

	.header-top{
		padding: unit((@header-height - @inner-height) / 2/ 25, rem) 0;
		background: #F9F9F9;
	}

	.top-back{
		z-index: @zindex + 3;
		position: absolute;
		top: 0;
		line-height: 44*@rex;
		left: 5*@rex;
		width: 30*@rex;

		>i{
			font-size: 24*@rex;
		}
	}

	input{
		user-select: initial;
		font-family: inherit;
	}

	.search-box{
		margin: 0 46*@rex;
		position: relative;
		font-size: 14*@rex;
		width: 60%;
		height: unit(@inner-height/25, rem);
		background: #eee;
		border-radius: 5px;

		i{
			.absolute(0,_,_,_);
			line-height: unit(@inner-height/25, rem);
			color: #a8a8a8;
			width: 36*@rex;
			text-align: center;
			font-size: 26*@rex;

			&.i-search{
				left: 0;
			}

			&.i-clear{
				right: 0;
			}
		}

		.search{
			border: none;
			height: unit(@inner-height/25, rem);
			background: #E5E5E5;
			border-radius: 4*@rex;
			display: block;
			outline: 0;
			padding: 0 36*@rex;
			width: 98%;
			font-size: 14*@rex;
			box-sizing: border-box;
		}
	}

	.header-filter{
		font-size: 14*@rex;
		height: unit(@header-height/25, rem);
		line-height: unit(@header-height/25, rem);
		background: #fff;
		overflow: hidden;

		.item{
			float: left;
			width: 33.3%;
			text-align: center;
			position: relative;

			&-active{
				color:@ht-color;

				&.item-price:after{
					color:@ht-color;
				}
			}

			&-up,&-down{
				color: @ht-color;

				&:after{
					margin-left: 8%;
					.tuan-icon();
				}
			}

			&-down:after{
				content: '\e606';
			}

			&-up:after{
				content: '\e605';
			}

			&-price:after{
				margin-left: 8%;
				.tuan-icon();
				content: '\e607';
			}

			&:before{
				content: '';
				height: 50%;
				.absolute(25%,0,_,_);
				width: 1px;
				background:@bd-color;
			}

			&:last-child{
				&:before{
					display: none;
				}
			}
		}
	}

	.mask{
		.fixed(0,0,0,0);
		background: rgba(0, 0, 0, 0.8);
		z-index: -1;
		opacity: 0;
		transition: opacity .3s ease;
	}

	.filter-box{
		background: #fff;
		opacity: 0;
		z-index: -1;
		.absolute(0,_,_,0);
		width: 100%;
		margin: 0 auto;
		max-width: unit(@container-max-width/25, rem);
		transition: opacity .3s ease;
		left: 0;
		top: 0;
		right: 0;

		.title{
			text-align: center;
			font-size: 18*@rex;
			line-height: 44*@rex;
		}
	}

	.sub-grid{
		@inner-height: 30;

		border-top: 1px solid @bd-color;
		padding: unit((@header-height - @inner-height) / 2/25, rem) 2%;
		height: unit(@header-height/25, rem);
		line-height: unit(@inner-height/25, rem);
		font-size: 14*@rex;

		> span{
			float: left;
		}

		.text{
			width: 30%;
			.ellipsis();
		}

		.input-box{
			width: 25%;

			input{
				height: unit(@inner-height/25, rem);
				padding-left: 5%;
				width: 100%;
				border: 1*@rex solid @bd-color;
				vertical-align: top;
			}
		}

		.hr{
			text-align: center;
			float: left;
			width: 5%;
		}

		.btn-box{
			width: 15%;
			text-align: right;

			a{
				background: @ht-color;
				border-radius: 0.06rem;
				color: #fff;
				display: inline-block;
				vertical-align: top;
				padding: 0 10%;
			}
		}
	}

	.header-inner.active{
		.mask{
			z-index: @zindex;
			opacity: 1;
		}

		.top-back{
			position: fixed;
		}

		.filter-box{
			position: fixed;
			z-index: @zindex;
			opacity: 1;
		}
	}
}
