@import "../_tuan-mixins.less";
@import "../_tuan-variables.less";

.fn-module-error{
	.error{
		&:before, &:after{
			content: '';
			display: block;
		}

		&:before{
			padding-top: 45%;
			background: data-uri('@{tuan-image-path}/tuan-error.png') bottom center no-repeat;
			background-size: 100% auto;
		}

		&:after{
			padding-bottom: 18.8%;
		}
	}


	.error-text{
		text-align: center;
		padding: 0 10%;
		color: #666;
		margin-top: 6%;
	}
}
